
import { defineComponent, ref, watchEffect, computed } from "vue";
import { useStore } from "vuex";
export default defineComponent({
  name: "Product",
  props: {
    prod: {
      type: Array,
      required: true,
    },
    position: {
      type: String,
      default: "top",
    },
  },
  setup(props, contex) {
    const position = ref(""),
      elements = ref(),
      store = useStore(),
      selectedProducts = computed(() => store.state),
      sortProduct = (pos: string, elem: any) => {
        const sorts = elem.sort(
          (
            a: { price: { current_price: number } },
            b: { price: { current_price: number } }
          ) => a.price.current_price - b.price.current_price
        );
        if (pos == "top") return sorts;
        else if (pos == "bottom") return sorts.reverse();
      },
      addBasket = (elem: object | any) => {
        elem.selected = true;
        store.commit("addBasketToStorage", {
          el: elem,
          storeName: "basket",
        });
      },
      addFavorites = (elem: object | any) => {
        store.commit("addBasketToStorage", {
          el: elem,
          storeName: "favorites",
        });
      };

    watchEffect(() => (elements.value = props.prod));
    watchEffect(() => (position.value = props.position));
    return {
      elements,
      position,
      selectedProducts,
      sortProduct,
      addBasket,
      addFavorites,
    };
  },

  created() {
    const store = useStore()
    if (localStorage.getItem("basket") !== null) {
      const localData = localStorage.getItem("basket");
      store.commit("addStorage", {
        el: localData,
        storeName: "basket",
      });
    }
    if (localStorage.getItem("favorites") !== null) {
      const localData = localStorage.getItem("favorites");
      store.commit("addStorage", {
        el: localData,
        storeName: "favorites",
      });
    }
  },
});
